@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #232526;  /* fallback for old browsers */
  background: url(/devchia254/starwars_api/static/media/starwars_bg.ee763277.jpg) no-repeat center center fixed;
  background-size: cover;
  /*background: -webkit-linear-gradient(to right, #414345, #232526);*/  /* Chrome 10-25, Safari 5.1-6 */
  /*background: linear-gradient(to right, #414345, #232526);*/ /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}


@font-face {
	font-family:"Star Jedi";
	src:url(/devchia254/starwars_api/static/media/Starjedi.7248a9a5.eot) format("eot"),
		url(/devchia254/starwars_api/static/media/Starjedi.6a5d63cf.woff) format("woff"),
		url(/devchia254/starwars_api/static/media/Starjedi.02980b30.ttf) format("truetype"),
		url(/devchia254/starwars_api/static/media/Starjedi.03f961de.svg#StarJedi) format("svg");
	font-weight:normal;
	font-style:normal;}

* {
	box-sizing: border-box;
}

h1 {
	font-family: 'star jedi';
	font-weight: 200;
	color: #fffc00;
}

h2, h3, h4, h5, h6, p {
	font-family: 'Roboto', sans-serif;
}
h2 {
	font-size: 3rem;
	color: #FFFF8D;
}

h2.Sub-title {
	margin-top: 0;
	margin-bottom: 0.25em;
	text-shadow: 1px 1px 1px #A39A4C,
	1px 2px 1px #A39A4C,
	1px 3px 1px #A39A4C,
	1px 4px 1px #A39A4C,
	1px 5px 1px #A39A4C,
	1px 12px 4px rgba(16,16,16,0.4),/* shadow */
	1px 16px 8px rgba(16,16,16,0.2),/* shadow */
	1px 15px 25px rgba(16,16,16,0.2),   
	1px 20px 40px rgba(16,16,16,0.4);
}

h1.Title {
	font-size: 6rem;
	color: black;
	text-shadow: 0px 0px 4px #FFFDE7, 0px 0px 8px #FFF59D, 0px 0px 38px #FFEE58;
	margin-bottom: 0.25em;
}

.Card:hover {
	box-shadow: 0 0 2px 1px #FFFDE7, 0 0 4px 2px #FFF59D, 0 0 16px 8px #FFEE58;
}
